.sudoku-game {
    flex-direction: colunmn;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: center; /* vertically aligns items */
    align-items: center; /* horizontally aligns items */
}

.cell-container {
    position: relative;
}

.cell-cluster-grid {
    position: absolute;
    width: "100%";
    height: "100%";
    //minWidth: size,
    border: '1px';
    border-color: 'gray.800';
    display: "flex";
}

.cell-grid-container {
    aspect-ratio: 1 / 1;
}

.cell-grid {
    aspect-ratio: 1 / 1;
    flex-grow: 1;
}